<template>
  <v-container
    id="listarVentas"
    fluid
    tag="section"
  >
    <v-btn
      to="/adv/ventas/nuevo"
      fab
      dark
      color="purple"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>

    <v-btn
      to="/adv"
      fab
      dark
      color="black"
      class="mx-5"
    >
      <v-icon>mdi-arrow-left</v-icon>
    </v-btn>

    <base-material-card
      icon="mdi-clipboard-text"
      title="Lista de Ventas"
      class="px-5 py-3"
    >
      <v-data-table
        :headers="cabecera"
        :items="ventas"
        sort-by="Fecha"
        class="elevation-3"
      >
        <template v-slot:item.situacion="{item}">
          <v-chip
            :color="getColor(item.situacion)"
            dark
          >
            {{ item.situacion }}
          </v-chip>
        </template>

        <template v-slot:item.actions="{item}">
          <v-btn
            fab
            small
            color="brown"
            :to="{name: 'Editar Venta', params: {id: item._id}}"
          >
            <v-icon>mdi-pencil-lock</v-icon>
          </v-btn>
          <v-btn
            fab
            small
            color="green"
            class="mx-2"
            :to="{name: 'Vista Venta', params: {id: item._id}}"
          >
            <v-icon>mdi-eye</v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </base-material-card>
  </v-container>
</template>
<script>
  import axios from 'axios'

  const urlVentas = 'https://sistema-adv.herokuapp.com/api/sale/'

  export default {
    name: 'ListarVentas',
    data () {
      return {
        cabecera: [
          { text: 'Fecha', value: 'fecha', align: 'center' },
          { text: 'Cliente', value: 'customer.name', align: 'center' },
          { text: 'Producto', value: 'vehicle.model', align: 'center' },
          { text: 'Vendedor', value: 'seller.name', align: 'center' },
          { text: 'Financiamiento', value: 'financing.type', align: 'center' },
          { text: 'Situacin', value: 'situacion', align: 'center' },
          { text: 'Acciones', value: 'actions', align: 'center', sortable: false },
        ],
        ventas: [
          {
            fecha: '',
            seller: '',
            vehicle: '',
            financing: '',
            entity: '',
            support: '',
            office: '',
            account_executive: '',
            customer: '',
            situacion: '',
          },
        ],
      }
    },
    mounted () {
      this.obtenerVentas()
    },
    methods: {
      obtenerVentas () {
        axios.get(urlVentas).then(res => {
          // console.log(res.data);
          this.ventas = res.data
        }).catch(e => {
          console.log(e)
        })
      },
      getColor (situacion) {
        if (situacion === 'Cancelado') return 'green'
        else if (situacion === 'Por Desembolsar') return 'orange'
        else if (situacion === 'Libre') return 'grey'
        else return 'red'
      },
    },
  }
</script>
